import validator from "validator";

export default class Login {
    constructor(formClass){ // passado como argumento a classe do formulrio.
        this.form = document.querySelector(formClass);
    }

    init(){
        this.events();
    }

    events(){
        if(!this.form) return;

        this.form.addEventListener("submit", (e) => {
            e.preventDefault();
            this.validate(e);
        });
    }

    validate(e){
        const el = e.target; //Ir armazenar o elemento alvo no evento
        const emailInput = el.querySelector("input[name='email']");
        const passwordInput = el.querySelector("input[name='password']");
        const divErrorEmail = el.querySelector("#emailError");
        const divErrorPassword = el.querySelector("#passwordError");

        //A seguinte parte do cdigo tem como funo remover o contedo da div caso j haja algum erro e torn-la hidden novamente, caso ela no esteja.
        divErrorEmail.innerText = "";
        divErrorPassword.innerText = "";
        divErrorEmail.setAttribute("hidden", "");
        divErrorPassword.setAttribute("hidden", "");

        //Flag para identificar se h erro ou no.
        let error = false;

        if(!validator.isEmail(emailInput.value)){
            divErrorEmail.removeAttribute("hidden");
            divErrorEmail.innerText = "O email  invlido.";
            error = true;
        }

        if(passwordInput.value.length < 3 || passwordInput.value.length > 50){
            divErrorPassword.removeAttribute("hidden");
            divErrorPassword.innerText = "A senha precisa conter entre 3 e 50 caracteres.";
            error = true;
        }

        if(!error) el.submit();
    }
}